































import { Component, Vue } from 'vue-property-decorator';
import { SongList, Loading } from '@/components';
import { getListByUser } from '../api';

@Component({
  components: {
    SongList,
    Loading,
  },
})
export default class MusicRadio extends Vue {
  public $refs !: {
    content: Element,
    audio: HTMLAudioElement,
  };

  private created() {
    this.loadData(+this.$route.params.id);
  }

  private data() {
    return {
      data: null,
    };
  }

  get isPlay() {
    return this.$store.state.player.isPlay;
  }

  get currentMusic() {
    return this.$store.state.player.musicList[this.$store.state.player.currentMusicIndex];
  }

  private selectSong(song: any) {
    this.$store.commit('player/add', song);
  }

  private play() {
    this.$store.commit('player/add', (this.data as any)[0]);
    this.$store.commit('player/play');
  }

  private pause() {
    this.$store.commit('player/pause');
  }

  private async loadData(id: number) {
    const res = await getListByUser({
      size: 100,
      page: 1,
      category: 1,
    });
    this.data = res;
  }
}
